.get-info {
  height: 500px;
  background: url("../app/img/bg/get-ingo-bg.png") 0 0/cover no-repeat;
  background-position: center;
}
.get-info .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.get-info .container .get-info_title {
  width: 400px;
  margin-bottom: 30px;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
  letter-spacing: 0.15px;
}